import { useRef } from "react";
import urls from "../../constants/urls";
import { TypeAnimation } from "react-type-animation";
import TextAnimation from "../toolbox/TextAnimation";

export default function AboutPage({ props }) {

  const text = `Merhaba! Ben bir bilgisayar mhendislii rencisiyim ve sizi burada karlamaktan mutluluk duyuyorum. Adm Halil nar ve bilgisayarlarla ilgili her eyi seviyorum!
ocukluumdan beri teknolojiye kar byk bir ilgim vard ve bu ilgi beni bilgisayar mhendislii alannda okumaya ynlendirdi. Bilgisayar Mhendislii blmnde okuyorum ve 2025 ylnda mezun olmay hedefliyorum.
Bilgisayar mhendislii ile ilgili ok eitli projelerde yer aldm. Programlama dilleri konusunda uzmanlk kazandm ve Java ,JavaScript,C# , Kotlin  gibi dillerde deneyimim var. Ayrca web ve mobil gelitirme gibi alanlarda React, Flutter  gibi  gncel teknolojilere de ilgi duyuyorum ve bunlarla ilgili de deneyimlerim oldu.
Ayrca kendimi srekli olarak gelitirmek iin abalyorum. Kendi kendime renme, yeni teknolojileri takip etme ve kendimi gncel tutma konusunda azimliyim. Ayn zamanda bakalaryla bilgi paylamaktan da keyif alyorum ve mesleimi srekli olarak ilerletmek iin kendimi motive ediyorum.
Hakkmda daha fazla bilgi edinmek iin portfy web sitesimi gezebilir veya benimle iletiim kurabilirsiniz.
Ekip almasna, teknolojiye ve srekli renmeye olan tutkum ile gelecekteki projelerde baarl olacama inanyorum!`;

  return (
    <section
    id="AboutPage"
      style={{
        background: "url(" + urls.aboutMePageBackground + ")",
        height: "90vh",
        backgroundSize: "cover",
        backgroundPosition: "center",
      }}
    >
      
      <div className="d-flex justify-content-between h-100">
        <div
         
          style={{
            flex: "3",
            alignSelf: "self-end",
            background: "url(" + urls.rightme + ")",
            height: "90%",
            backgroundSize: "110% 110%",
            backgroundBlendMode: "difference",
            backgroundPosition: "center",
            margin: "0 10px 10px 0",
          }}
        >
          
        </div>
        <div
          style={{
            flex: "5",
            padding: "10px",
            display: "grid",
            justifyContent: "center",
            color: "white",
          }}
        >
         
    /*<pre
            style={{
           
            whiteSpace:"break-spaces",
            fontWeight:"bolder",
            textAlign:"center",
            fontFamily:"sans-serif",
            fontStretch:"extra-condensed",
            fontStyle:"oblique",
            textShadow:" 3px 3px 2px rgba(0, 0, 0, 1)"
            }}
          > <TypeAnimation
          sequence={[text]}
          wrapper="span"
          cursor={true}
          speed={50}
          
          style={{ fontSize: '24px', color:"white",fontWeight:"700" }}
        />
           
          </pre>*/
        </div>
      </div>
    </section>
  );
}
